import { convertStylesToCssCode, getCusomPadding } from '@helper';

// edit用: <style>タグに出力するもの
export function getDynamicStyles({ height, heightPC, heightSP, paddingPC, paddingSP, clientId }) {
	const styles = {
		all: {
			'--arkb-padding': paddingPC ? getCusomPadding(paddingPC, '0') : '',
		},
		sp: {
			'--arkb-padding': paddingSP ? getCusomPadding(paddingSP, '0') : '',
		},
	};

	// min-height
	if ('custom' === height) {
		if (heightPC) styles.all['--arkb-min-height'] = heightPC;
		if (heightSP) styles.sp['--arkb-min-height'] = heightSP;
	}

	return convertStylesToCssCode(`#block-${clientId}`, styles);
}

// edit用: 普通にstyle属性に出力するもの
export const getBlockEditStyle = ({ textColor, isRepeat, media, bgSize, svgTopH, svgBottomH }) => {
	const style = {
		color: textColor || null,
		'--arkb-svg-height--top': 0 !== svgTopH ? `${svgTopH}vw` : null,
		'--arkb-svg-height--bottom': 0 !== svgBottomH ? `${svgBottomH}vw` : null,
	};

	// リピート背景画像
	if (isRepeat && media.url) {
		style.backgroundImage = `url(${media.url})`;
		style.backgroundRepeat = 'repeat';

		if (bgSize) {
			style.backgroundSize = bgSize;
		}
	}

	return style;
};

export const getColorStyle = ({ bgColor, bgGradient, opacity }) => {
	if (!bgGradient && !bgColor) return null;
	const style = {};

	// グラデーションかどうか
	if (bgGradient) {
		style.background = bgGradient;
	} else {
		style.backgroundColor = bgColor || null;
	}
	style.opacity = (opacity * 0.01).toFixed(2);
	return style;
};

export const getSvgData = (svgData) => {
	const svgLevel = svgData.level || 0;
	if (0 === svgLevel) {
		return {
			...svgData,
			height: 0,
			isReverse: false,
		};
	}

	// vwに合わせて 100 >> 10.0
	const svgHeight = (svgLevel * 0.1).toFixed(1);
	return {
		...svgData,
		height: Math.abs(svgHeight), // 絶対値
		isReverse: 0 > svgLevel, // 負の値かどうか
	};
};
